@mixin transition {
    transition: all 0.35s ease-in-out;
}  
@mixin transition-img {
    transition: transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94);       
}
.section-banner-multiple {
    .banner-item {
        overflow: hidden;
        @include transition; 
        > a {
            color: inherit;
        }
        > .image__style img, a > .image__style img {
            @include transition-img;
        } 
        &:hover {
            @media (hover: hover) {
                > .image__style img, a > .image__style img {
                    transform: scale(1.2);    
                }  
            } 
        }
    }
}